import React, {Component} from 'react';
import PropTypes from 'prop-types';

import {  Form, Input, Select, Icon, Button, Row, Col, Card } from 'antd';
const { TextArea } = Input;

const Option = Select.Option;
const FormItem = Form.Item;
const createForm = Form.create;

export class PageForm extends Component {

  static propTypes = {
    formValues: PropTypes.object,
    form: PropTypes.object,
    termlist: PropTypes.array,
    handleOK: PropTypes.func
  }

  handleSubmit = (e) => {
    const form = this.props.form;
    const fields = this.props.form.getFieldsValue();
    const page_status = e.target.dataset.status;
    if (this.props.formValues && this.props.formValues._id) {
      fields._id = this.props.formValues._id;
    }
    fields.page_status = page_status;
    form.validateFields((err) => {
      if (!err) {
        this.props.handleOK(fields, (err) => {
          console.log(err);

          const errObj = {};
          for (const i in err.errors) {
            if (err.errors[i]) {
              errObj[i] = {
                value: err.errors[i].value,
                errors: [new Error(err.errors[i].message)]
              }
            }
          }
          form.setFields(errObj);
        });
      }
    });
  }

  render() {

    const { getFieldDecorator } = this.props.form;
    const {termlist} = this.props;
    const defaultSelect = termlist.length > 0 ? termlist[0]._id : '';
    const domain = window.INIT_DATA.domain;

    return (
      <div>
        <Form layout="horizontal" onSubmit={this.handleSubmit}>
          <Row>
            <Col span={18}>
              <FormItem
                hasFeedback
              >
                {getFieldDecorator('page_title', {
                  rules: [{ required: true, message: '文章标题不能为空!' }],
                })(
                  <Input placeholder="填写文章标题" autoComplete="off" />
                )}
              </FormItem>

              <FormItem
                hasFeedback
              >
                {getFieldDecorator('page_slug', {
                  rules: [{
                    required: true, message: '缩略名不能为空!!',
                  }],
                })(
                  <Input addonBefore={`${domain}/page`} addonAfter=".html&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;" placeholder="文章缩略名" autoComplete="off" />
                )}
              </FormItem>

              <FormItem
                hasFeedback
              >
                {getFieldDecorator('page_content', {
                  rules: [{
                    required: true, message: '内容不能为空!!',
                  }],
                })(
                  <TextArea type="content" placeholder="内容" autoComplete="off" rows="20" />
                )}
              </FormItem>


            </Col>
            <Col span={6} style={{paddingLeft: '20px'}}>

              <Card title="分类管理">
                <FormItem
                  label="所属分类"
                  hasFeedback
                >
                  {getFieldDecorator('page_term', {
                    initialValue: defaultSelect,
                    rules: [
                      { required: true, message: '所属分类不能为空!' },
                    ],
                  })(
                    <Select>
                      {
                        termlist.map((val) => {
                          return <Option value={val._id} key={val._id}>{val.term_name}</Option>
                        })
                      }
                    </Select>
                  )}
                </FormItem>
              </Card>

              <Card title="发布" className="mt20">
                <Button type="primary" size="large" data-status="1" onClick={this.handleSubmit}>立即发布</Button>
                <Button type="ghost" size="large" data-status="0" onClick={this.handleSubmit} style={{marginLeft: '10px'}}>保存草稿</Button>
              </Card>

            </Col>
          </Row>
        </Form>
      </div>
    )
  }

}

export default createForm({
  mapPropsToFields(props) {
    if (!props.formValues) {
      return {}
    }
    const {page_title, page_slug, page_content, page_term} = props.formValues;
    return {
      page_title: {
        ...page_title,
        value: page_title,
      },
      page_slug: {
        ...page_slug,
        value: page_slug
      },
      page_content: {
        ...page_content,
        value: page_content
      },
      page_term: {
        ...page_term,
        value: page_term
      }
    };
  }
})(PageForm);



