import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';

import { Table, Menu, Popconfirm, message } from 'antd';


import pageActions from '../action'
import appActions from '../../app/action'
import termActions from '../../term/action'
import PageForm from './PageForm';

export class PageDetail extends Component {

  static propTypes = {
    params: PropTypes.object,
    appActions: PropTypes.object,
    termActions: PropTypes.object,
    pageActions: PropTypes.object,
    router: PropTypes.object,
    page: PropTypes.object
  }

  state = {
    termlist: []
  }

  componentDidMount () {

    const {appActions, termActions, pageActions} = this.props;
    appActions.loading(true).then(() => {
      return termActions.getList({type: 'page'});
    }).then((doc) => {
      this.setState({
        termlist: doc.termlist
      });
      appActions.loading(false);
    }).catch(err => {
      message.error(err.msg);
    });
    this.getPageDetail(this.props.params._id);
  }

  componentWillReceiveProps (props) {
    if (props.params._id && this.props.params._id !== props.params._id) {
      this.getPageDetail(props.params._id);
    }
  }

  /**
   * 获取文章详情
   */
  getPageDetail (_id) {
    const {appActions, termActions, pageActions} = this.props;
    pageActions.getDetail({_id});
  }

  /**
   * 提交文章表单
   * @param formdata
   */
  handlePageSubmit = (formdata, callback) => {
    const {pageActions, router} = this.props;
    if (this.props.params._id) {
      formdata._id = this.props.params._id;
      pageActions.postEditPage(formdata).then(data => {
        pageActions.getList({page: 1}).then(data => {
          router.push({
            pathname: 'page'
          });
        })
      }).catch(err => {
        if (typeof err.msg === 'object') {
          callback(err.msg)
        } else {
          message.error(err.msg)
        }
      });
    } else {
      pageActions.postAddPage(formdata).then(data => {
        pageActions.getList({page: 1}).then(data => {
          router.push({
            pathname: 'page'
          });
        })
      }).catch(err => {

        if (typeof err.msg === 'object') {
          callback(err.msg)
        } else {
          message.error(err.msg)
        }

      });
    }
  }

  render() {
    const pagedetail = this.props.page.get('pagedetail');
    return (
      <div>
        <PageForm
          formValues={pagedetail}
          termlist={this.state.termlist}
          handleOK={this.handlePageSubmit}
        />
      </div>
    )
  }

}

export default connect((state) => {
  return  {
    page: state.get('page')
  }
}, dispatch => {
  return {
    pageActions: bindActionCreators(pageActions, dispatch),
    appActions: bindActionCreators(appActions, dispatch),
    termActions: bindActionCreators(termActions, dispatch),
  }
})(PageDetail);



